eline th {\n  vertical-align: middle;\n}\n.fc-timeline .fc-head .fc-cell-content {\n  padding-top: 3px;\n  padding-bottom: 3px;\n}\n.fc-timeline .fc-head .fc-time-area .fc-cell-content {\n  overflow: visible;\n}\n/* Time Area\n--------------------------------------------------------------------------------------------------*/\n.fc-time-area col {\n  min-width: 2.2em;\n  /* detected by JS */\n}\n/* head */\n.fc-ltr .fc-time-area .fc-chrono th {\n  text-align: left;\n}\n.fc-rtl .fc-time-area .fc-chrono th {\n  text-align: right;\n}\n/* body slats (vertical lines) */\n.fc-time-area .fc-slats {\n  /* fc-bg is responsible for a lot of this now! */\n  position: absolute;\n  z-index: 1;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n}\n.fc-time-area .fc-slats table {\n  height: 100%;\n}\n.fc-time-area .fc-slats .fc-minor {\n  border-style: dotted;\n}\n.fc-time-area .fc-slats td {\n  border-width: 0 1px;\n  /* need to do this. sometimes -1 margin wouldn't hide the dotted */\n}\n.fc-ltr .fc-time-area .fc-slats td {\n  border-right-width: 0;\n}\n.fc-rtl .fc-time-area .fc-slats td {\n  border-left-width: 0;\n}\n/* body content containers\n   can be within rows or directly within the pane's content\n*/\n.fc-time-area .fc-bgevent-container,\n.fc-time-area .fc-highlight-container {\n  position: absolute;\n  z-index: 2;\n  /* only for directly within pane. not for row. overridden later */\n  top: 0;\n  bottom: 0;\n  width: 0;\n}\n.fc-ltr .fc-time-area .fc-mirror-container,\n.fc-ltr .fc-time-area .fc-bgevent-container,\n.fc-ltr .fc-time-area .fc-highlight-container {\n  left: 0;\n}\n.fc-rtl .fc-time-area .fc-mirror-container,\n.fc-rtl .fc-time-area .fc-bgevent-container,\n.fc-rtl .fc-time-area .fc-highlight-container {\n  right: 0;\n}\n.fc-time-area .fc-bgevent,\n.fc-time-area .fc-highlight {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n}\n/* Now Indicator\n--------------------------------------------------------------------------------------------------*/\n.fc-timeline .fc